@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap');

/* targets every element*/
* {
  background-color: #EBEDDF;
  margin: 0;
  font-family: "Quicksand";
  font-weight: 400;
  color: #384b2d;
}

.navbar {
  padding: 20px;
  display: flex;
  max-width:600px;
  align-items: center;
  margin: 0 auto;
  border-bottom: 1px solid #1F2B37;
}

.navbar h1 {
  color: #384b2d;
  font-weight: bold;
}
.navbar .links {
  margin-left:center;
}
.navbar a {
  text-decoration: none;
  font-weight: 800;
  padding: 6px;
}
.navbar a:hover{
  color: #86679a;
}
.navbar .icons{
  margin-left: auto;
}



.content {
  max-width: 600px;
  margin: 40px auto;
  padding: 20px;

}

.project-preview {
  padding: 10px 16px;
  margin: 20px 0;
  background-color: #c6cec1;
  border-bottom: 10px solid #585858;
  box-shadow: 3px 10px 3px rgb(153, 153, 153);
  border-radius: 20px
}

.project-preview:hover {
  box-shadow: 3px 10px 3px #86679a;
}
.project-preview h2 {
  background-color: #c6cec1;
  font-size: 25px;
  font-weight: bold;
  color: #384b2d;
  margin-bottom: 8px;
}
.project-preview a {
  text-decoration: none;
}
.project-preview p {
  background-color: #c6cec1;
  text-decoration: none;
}

.project-details h2 {
  font-size: 20px;
  color: #384b2d;
  margin-bottom: 10px;
}

.project-details div {
  margin: 20px 0;
}